import url from '@/request/url.js'
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			showTip: true,
			plans: [],
			uPlans: [], // uncomplete
			cPlans: [], // complete 
			tPlans: [] // timeout
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.userInfo
		})
	},
	methods: {
		operate(id) {
			uni.showModal({
				content: "确认要删除这条计划吗？",
				success: res => {
					if (res.confirm) {
						uni.request({
							url: url.delPlan + id,
							method: "DELETE",
							success: resp => {
								uni.showToast({
									title: '删除成功！'
								});
								this.getAllPlan(response => {
									this.sort(response.data);
								});
							}
						});
					}
				}
			});
		},
		updStatus(id) {
			let plan = this.plans.find((val, index, arr) => val.id == id);
			let status = 0;
			if (plan.status == 1) {
				status = 0;
			} else {
				status = 1;
			}
			uni.request({
				url: url.updPlanStatus,
				method: "GET",
				data: {
					id: id,
					status: status
				},
				success: resp => {
					this.getAllPlan(response => {
						this.sort(response.data);
					});
				}
			});
		},
		sort(data) {
			console.log(data)
			this.plans = data;
			this.uPlans.length = 0;
			this.cPlans.length = 0;
			this.tPlans.length = 0;
			data.forEach(p => {
				if (p.status == 0) {
					this.uPlans.push(p)
				} else if (p.status == 1) {
					this.cPlans.push(p);
				} else {
					this.tPlans.push(p);
				}
			});
		},
		toPath(e) {
			uni.navigateTo({
				url: 'add-plan/add-plan',
				animationType: "slide-in-right",
				animationDuration: 100
			})
		},
		getAllPlan(callback) {
			uni.request({
				url: url.getAllPlan,
				method: "GET",
				data: {
					uid: this.userInfo.id
				},
				success: callback
			});
		}
	},
	onShow() {
		this.getAllPlan(resp => {
			this.sort(resp.data);
			if (resp.data.length != 0) {
				this.showTip = false;
			}
		});
	},
	onPullDownRefresh() {
		this.getAllPlan(resp => {
			this.sort(resp.data);
			uni.stopPullDownRefresh();
		});
	}
}
